* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  margin: 25px;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
img{
  margin-right: 15px;
}

.country-name {
  font-size: 42px;
  text-shadow: 2px 2px 4px #d45353;
  color: #474747;
}

ul {
  list-style: none;
  padding: 0px;

}

li{
  font-size: 26px;
}

.description_item {
  font-weight: 700;
}

  h2{
    font-weight: normal;
  }
